.avatar {
   display: inline-block;
   overflow: hidden;
   line-height: 1;
   vertical-align: middle;
   background-color: var(--color-avatar-bg);
   border-radius: 6px;
   flex-shrink: 0;
   box-shadow: 0 0 0 1px var(--color-avatar-border);
   border-radius: 50%;
   
   }

   .flex-auto {
      flex: auto !important;
  }
  .flex-items-center {
   align-items: center !important;
}
.flex-justify-between {
   justify-content: space-between !important;
}
.app__works {
   flex: 1;
   width: 100%;
   flex-direction: column;
}

.app_work-filter {
   display: flex;
   flex-direction: row;
   justify-content: flex-start;
   align-items: center;
   flex-wrap: wrap;
   margin: 4rem 0 2rem;

   .app__work-filter-item {
      padding: 0.5rem 1rem;
      border-radius: 0.5rem;
      background-color: #ffffff;
      color: #000;
      font-weight: 800;
      cursor: pointer;
      transition: all 0.3s ease;
      margin: 0.5rem;

      &:hover {
         background-color: var(--secondary-color);
         color: #ffffff;
      }

      @media screen and (min-width: 2000px) {
         padding: 1rem 2rem;
         border-radius: 0.85rem;
         &:hover {
            background-color: var(--secondary-color);
            color: #ffffff;
         }
      }
   }

   .item-active {
      background-color: var(--secondary-color);
      color: #ffffff;
   }
}

.app__work-portfolio {
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;

   .app__work-item {
      width: 270px;
      flex-direction: column;
      margin: 2rem;
      padding: 1rem;
      border-radius: 0.5rem;
      background-color: #fff;
      color: #000;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
         box-shadow: 0 0 25px rgba(146,200,62,0.87);
      }
      @media screen and (min-width: 2000px) {
         width: 470px;
         padding: 1.25rem;
         border-radius: 0.75rem;
      }
      @media screen and (max-width: 300px) {
         width: 100%;
         margin: 1rem;
      }
   }
}
.app__work-img {
   width: 100%;
   height: 230px;
   position: relative;
   img {
      width: 100%;
      height: 100%;
      border-radius: 0.5rem;
      object-fit: cover;
   }

   @media screen and (min-width: 2000px) {
      height: 350px;
   }
}

.app__work-hover {
   position: absolute;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
   width: 100%;
   height: 100%;
   background-color: rgba(146,200,62,0);
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: center;
   border-radius: 0.5rem;
   opacity: 0;
   transition: all 0.3s ease;

   div {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: rgba(146,200,62,0.87);
      color: #fff;
      margin: 1rem;
      font-family: var(--font-base);
      font-weight: 800;
      cursor: pointer;
      transition: all 0.3s ease;

      svg {
         width: 50%;
         height: 50%;
         color: var(--white-color);
      }
   }
}

.app__work-content {
   padding: 0.5rem;
   width: 100%;
   position: relative;
   flex-direction: column;

   h4 {
      margin-top: 1rem;
      line-height: 1.5;
   }
   .app__work-tag {
      position: absolute;
      padding: 0.5rem 1rem;
      border-radius: 10px;
      background-color: #fff;
      top: -25px;
   }
}
