.app {
   background-color: var(--primary-color);
//   background-color: #000;
   font-family: var(--font-base);

   // background:linear-gradient(0deg, rgba( 7, 63, 78 ,0.87), rgba( 7, 63, 78 ,0.87)), url('assets/GB0A0079.jpg');
   background-size:cover;
   margin-top: -50px;
}

.app__whitebg {
   background-color: var(--white-color);
}

.app__primarybg {
   background-color: var(--primary-color);
}

.app__container {
   width: 100%;
   min-height: 100vh;
   display: flex;
   flex-direction: row;
}

.app__flex {
   display: flex;
   justify-content: center;
   align-items: center;
}

.app__wrapper {
   flex: 1;
   width: 100%;
   flex-direction: column;
   padding: 4rem 2rem;

   @media screen and (max-width: 450px) {
      padding: 4rem 1rem 2rem;
   }
}



.head-text {
   font-size: 2.75rem;
   font-weight: 800;
   text-align: center;
   color: var(--black-color);
   text-transform: capitalize;

   span {
      color: var(--secondary-color);
   }

   @media screen and (min-width: 2000px) {
      font-size: 4rem;
   }

   @media screen and (max-width: 450px) {
      font-size: 2rem;
   }
}

.p-text {
   font-size: 0.8rem;
   text-align: left;
   color: var(--gray-color);
   line-height: 1.5;

   @media screen and (min-width: 2000px) {
      font-size: 1.75rem;
   }
}

.bold-text {
   font-size: 1rem;
   font-weight: 800;
   color: var(--black-color);
   text-align: left;

   @media screen and (min-width: 2000px) {
      font-size: 2rem;
   }

   @media screen and (max-width: 450px) {
      font-size: 0.9rem;
   }
}

.app__social {
   display: flex;
   justify-content: flex-end;
   align-items: center;
   flex-direction: row;

   padding: 1rem;

   div {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: var(--white-color);
      margin: 0.25rem 0;
      border: 1px solid var(--lightGray-color);
      display: flex;
      justify-content: center;
      align-items: center;

      transition: all 0.3s ease-in-out;

      svg {
         width: 15px;
         height: 15px;
         color: var(--gray-color);
      }

      &:hover {
         background-color: var(--secondary-color);
         border-color: var(--secondary-color);

         svg {
            color: var(--white-color);
         }
      }

      @media screen and (min-width: 2000px) {
         width: 70px;
         height: 70px;

         margin: 0.5rem 0;

         svg {
            width: 30px;
            height: 30px;
         }
      }
   }
}


@media screen and (max-width: 500px) {
   .app__navigation,
   .app__social {
      // display: none;
   }

   
}
